import React, { useEffect, useState, useCallback } from "react"
import { Row, Col, List } from "antd"
import { withRouter } from "react-router-dom"
import styled from "styled-components"

import { Section, Heading, Layout } from "components"
import FilteringPanel from "./_FilteringPanel"
import TutorCard from "components/common/TutorCard"
import SelectInput from "components/forms/SelectInput"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { country } from "App"
import { countryMap } from "helpers/countryMapping"
import { useTutors } from "helpers/hooks/data"

const TutorList = styled(List)`
	.ant-row {
		display: flex;
		flex-wrap: wrap;
		.ant-col {
			margin-bottom: 0;
			.ant-list-item {
				height: 350px;
			}
		}
	}
`

function Marketplace({ location, history, i18n }) {
	const initialPriceRange = (country && countryMap[country].pricePerHourRange.range) || []

	const initialValues = {
		keywords: "",
		curriculums: 0,
		subjects: 0,
		alphabet: 0,
		recently: 0,
		location: "",
		nonAcademicPvid: "all",
		price_range: { startPrice: initialPriceRange[0], endPrice: initialPriceRange[1] }
	}

	const [formValues, setFormValues] = useState(initialValues)

	const [searchUsers, { loading, data: users }] = useTutors()

	const handleSortUsers = (value) => {
		const recently = value === "recently" ? 1 : 0
		const alphabet = value === "alphabet" ? 1 : 0
		const values = { ...formValues, country, recently, alphabet }
		setFormValues((prev) => ({ ...prev, country, recently, alphabet }))
		searchUsers({ variables: values })
	}

	const handleSearchFilter = useCallback(
		(name) => (e) => {
			const { price_range } = formValues
			setFormValues((prev) => ({ ...prev, [name]: e.target.value }))

			const values = {
				...formValues,
				[name]: e.target.value,
				price_range: { startPrice: price_range.startPrice, endPrice: price_range.endPrice }
			}

			if (window.location.search !== "") history.push(`/${country}/lifelong/browse`)
			searchUsers({ variables: values })
		},
		[formValues, history, searchUsers, setFormValues]
	)

	const handleChangeSlider = useCallback(
		(value) => {
			setFormValues((prev) => ({ ...prev, price_range: { startPrice: value[0], endPrice: value[1] } }))
			const values = {
				...formValues,
				price_range: { startPrice: value[0], endPrice: value[1] }
			}

			if (window.location.search !== "") history.push(`/${country}/lifelong/browse`)
			searchUsers({ variables: values })
		},
		[formValues, history, searchUsers, setFormValues]
	)

	const handleClickTutor = (item) => {
		// 	onMarketplace.setValueFromMarketplace({
		// 		keywords: formValues.keywords,
		// 		subjects: subjectLabel,
		// 		curriculums: curriculumLabel,
		// 	})
		window.open(`/${country}/${item.token}--${item.pvid}--${item.firstName}--${item.lastName}`)
	}

	useEffect(() => {
		// if (subjectParams) {
		// 	searchUsers({ variables: { ...formValues, subjects: Number(subjectParams) } })
		// } else if (keywordParams) {
		// 	searchUsers({ variables: { ...formValues, keywords: keywordParams } })
		// } else searchUsers({ variables: formValues })
		searchUsers({ variables: formValues })
	}, [formValues, searchUsers])

	return (
		<Layout>
			<Section centered maxWidth="1200px">
				<Row gutter={48}>
					<Col lg={6}>
						<FilteringPanel
							onUsers={{ handleSearchFilter, handleChangeSlider }}
							onFormValues={{ formValues, setFormValues }}
							location={{ location, history }}
						/>
					</Col>
					<Col lg={18}>
						<Section noPadding className="mb2em smooth-scroll">
							<Row type="flex" justify="space-between" align="middle">
								<Col lg={12}>
									<Heading
										content={<Trans>Searches</Trans>}
										subheader={
											<Trans>
												<span>
													Showing&nbsp;
													<strong>{users.length} tutors</strong>
												</span>
											</Trans>
										}
										level={4}
									/>
								</Col>
								<Col lg={12} xs={12} style={{ textAlign: "right" }}>
									<Row gutter={16} type="flex" justify="end">
										<Col lg={12}>
											<SelectInput
												independent
												name="sort"
												defaultvalue={<Trans>Sort by...</Trans>}
												options={[
													{ value: "Sort by...", label: <Trans>Sort by...</Trans> },
													{ value: "recently", label: <Trans>Recently added</Trans> },
													{ value: "alphabet", label: <Trans>Alphabet</Trans> }
												]}
												onChange={handleSortUsers}
												placeholder={i18n._(t`Sort by...`)}
											/>
										</Col>
									</Row>
								</Col>
							</Row>
						</Section>
						<Section noPadding>
							<TutorList
								dataSource={users}
								grid={{ gutter: 16, xs: 2, lg: 4 }}
								loading={loading}
								pagination={{ onChange: () => window.scrollTo({ top: 0, behavior: "smooth" }), pageSize: 12 }}
								renderItem={(item) => {
									return (
										<List.Item key={item.pvid}>
											<TutorCard {...item} onClick={() => handleClickTutor(item)} />
										</List.Item>
									)
								}}
							/>
						</Section>
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

export default withI18n()(withRouter(Marketplace))
